body{margin:0;height:100vh;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:linear-gradient(to right top, #051937, #0a2040, #102849, #152f53, #1a375c);background-attachment:fixed;color:#fff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}@media print{@page{margin:0}.no-print{display:none}.App{color:#000}}

.App{display:flex;flex-direction:column;justify-content:space-between;align-items:center;min-height:100vh;height:100%}.App__buttons{display:flex;grid-column-gap:12px;-webkit-column-gap:12px;column-gap:12px}.App__footer{font-size:10px;padding:8px}.App__passports{width:595px;margin:0}.button{padding:2px 16px;height:40px;cursor:pointer;border:none;border-radius:4px;border:2px solid #676aff;background:rgba(103,106,255,0.2);box-shadow:0px 3px 1px -2px rgba(0,0,0,0.25),0px 2px 2px 0px rgba(0,0,0,0.3),0px 1px 5px 0px rgba(0,0,0,0.25);color:#fff;transition:all .3s ease-in-out}.button:hover{background:#676aff;transition:all .3s ease-in-out}.button-warning{padding:2px 16px;height:40px;cursor:pointer;border:none;border-radius:4px;border:2px solid #da1e4d;background:rgba(218,30,77,0.2);box-shadow:0px 3px 1px -2px rgba(0,0,0,0.25),0px 2px 2px 0px rgba(0,0,0,0.3),0px 1px 5px 0px rgba(0,0,0,0.25);color:#fff;transition:all .3s ease-in-out}.button-warning:hover{background:#da1e4d;transition:all .3s ease-in-out}

.AddPassport__header{font-weight:600}.AddPassport__error{color:red;font-size:smaller}.AddPassport__form{display:flex;flex-direction:column}.AddPassport__input{padding:.5rem;margin-bottom:1rem}

.PassportsToPrint{width:100%;page-break-inside:avoid;break-inside:avoid-page;padding:1rem;box-sizing:border-box}.PassportsToPrint:hover{background:rgba(8,6,17,0.267)}.PassportsToPrint__button{width:15px;height:15px;padding:unset;background:none;border:none;border-radius:50%;font-size:8px;color:#da1e4d;cursor:pointer}.table{font-weight:600;width:100%;table-layout:fixed;color:#000}.table tr{display:flex;width:100%;padding:0 1px}.table__head-cell{display:flex}.table__header{font-weight:600;display:inline;margin:0;font-size:1.1rem}.table__cell--letter{width:5%}.table__cell--data{width:45%;height:-webkit-fit-content;height:fit-content;text-overflow:clip}.table__flag{width:60px;margin-right:1rem}.Passport{width:100%;padding:1px;border:1px solid black;background:#fff}.Passport__paragraph{margin:5px 0 4px}.Passport__box{display:flex;align-items:center;justify-content:space-between}.DeletePassport__button{width:40px;height:40px;margin-left:14px;border-radius:50%;display:flex;justify-content:center;align-items:center}.DeletePassport__icon{width:18px}@media print{@page{margin:0}.no-print{display:none}}

